import React from 'react';
import mine from '../images/mine.jpg';
import Image from 'react-bootstrap/Image';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


import '../css/aboutMe.css';

function AboutMe() {
  return (
    <div className="AboutMe container">

  <Container>
      <Row>
        <Col xs={8} className='AboutText' >
        <h5>About me</h5>
        <h3>DevOps Engineer</h3>
        <p>Currently working in Amadeus Software Labs, Bangalore</p>
        <p>Expertise in various technologies like Docker, Jenkins, Ansible, Python, Shell.</p>
        <p>Product Management, Product Design enthusiast</p>
        <p>#Sole goal in my life: Live upto my name’s meaning. Divit stands for Immortal.</p>
        <p>#Lovetotravel #Foodie #Learningtocook #WanttobeIndependent </p>
        <a href="https://drive.google.com/file/d/1PDlOnjNMeUxkiwvyT-3B4cnDg7RuvwfS/view?usp=sharing">  <button type="button" class="btn btn-dark">Resume</button></a>

        </Col>

        <Col xs={4}>
          <Image className='mine img-fluid img-thumbnail' src={mine} roundedCircle width={300} height={300} alt='loading..' />
        </Col>
      </Row>
    </Container>
    </div>
  );
}

export default AboutMe;
